.popup__title {
  font-size: 24px;
  line-height: 1.2;
  font-weight: 900;
  margin: 0;
  padding: 0;
}

@media all and (max-width: 548px) {
  .popup__title {
    font-size: 5.6vw;
    margin-bottom: 75px;
  }
}

@media all and (max-width: 320px) {
  .popup__title {
    font-size: 18px;
  }
}
